.main-nav {
  position: relative;
  width: auto;
  background-color: $bgcolorgr;
  @media (min-width: $desktop-width) {
    background-color: rgba(0, 0, 0, 0.3);

  }
  @media (min-width: $desktop-width) {
    display: flex;

  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;

}
.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none;

  &:hover,
  &:active {
    background-color: transparent;
  }
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;

}
.logo-nav{
  @media (min-width: $desktop-width){
    display: none;
  }
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: fade(#ffffff, 30%);
  box-shadow:
    0 8px 0 0 fade(#ffffff, 30%),
    0 16px 0 0 fade(#ffffff, 30%);
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  left: auto;


  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 24px;
    left: 18px;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: fade(#ffffff, 30%);
  }
}

.site-list__item {
  @media (min-width: $desktop-width) {
   margin-right: 32px;

   &:last-child {
     margin-right: 0;
   }
 }

}

.site-list__item a[href] {
 @media (min-width: $desktop-width) {
   &:hover {
     color: $colorhover;


   }

   &:active {
     color: #ffffff;


     opacity: 0.3;
   }
 }
}

.site-list{
  @media (min-width: $desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    width: 630px;
  }

}

.site-list a {
  position: relative;
  display: block;
  padding: 12px 21px;
  padding-bottom: 13px;
  font-size: 18px;
  line-height: 24px;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: $bgcolorgr;
  border-top: 1px solid  rgba(255, 255, 255, 0.15);
  border-bottom: 1px solid #000;
  @media (min-width: $desktop-width){
    background-color: transparent;
    border-top: none;

  border-bottom: none;
  }
}
.main-nav--closed .site-list__item {
  display: none;

}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}

.main-nav--nojs .main-nav__wrapper {
  position: static;
  min-height: 0;
}

.main-nav--nojs .site-list__item {
  display: block;
}






.main-nav__wrapper {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    width: 640px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 960px;
  }

}


.site-list {
  @media (min-width: $tablet-width) {
    width: 570px;
  }

  @media (min-width: $desktop-width) {
    width: 780px;
  }
}


.user-list {
  @media (min-width: $tablet-width) {
    width: 60px;
  }
}

